.xl-radio {
  position: relative;
  display: inline-block;
  white-space: nowrap;
  vertical-align: middle;
  line-height: 1; }
  .xl-radio__input {
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    z-index: 1;
    box-sizing: border-box;
    cursor: pointer; }
  .xl-radio__inner {
    position: relative;
    display: inline-block;
    left: 0;
    top: 0;
    width: 1em;
    height: 1em;
    border: 1px solid #dddee1;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    transition: all 0.25s ease-in-out; }
    .xl-radio__inner:after {
      display: block;
      content: '';
      background-color: #409eff;
      border-radius: 50%;
      width: 100%;
      height: 100%;
      transition: all 0.25s ease-in-out;
      transform: scale(0); }
  .xl-radio__label {
    padding-left: 0.3em; }

.xl-radio--border {
  border: 1px solid #dddee1;
  padding: 0.4em 0.6em;
  border-radius: 0.2em;
  transition: border-color 0.25s ease-in-out; }
  .xl-radio--border.xl-radio--checked {
    border-color: #409eff; }
  .xl-radio--border.xl-radio--focused {
    box-shadow: 0 0 0 0.15em rgba(64, 158, 255, 0.3); }
    .xl-radio--border.xl-radio--focused .xl-radio__inner {
      box-shadow: none; }

.xl-radio--disabled {
  opacity: 0.6; }
  .xl-radio--disabled .xl-radio__input {
    cursor: not-allowed; }

.xl-radio--readonly {
  pointer-events: none; }

.xl-radio--focused .xl-radio__inner {
  box-shadow: 0 0 0 0.15em rgba(64, 158, 255, 0.3); }

.xl-radio--checked .xl-radio__inner {
  border-color: #409eff; }
  .xl-radio--checked .xl-radio__inner:after {
    transform: scale(0.7); }

.xl-radio--checked .xl-radio__label {
  color: #409eff; }

.xl-radio:hover:not(.xl-radio--disabled) .xl-radio__inner {
  border-color: #73b8ff; }

.xl-radio:hover:not(.xl-radio--disabled).xl-radio--border {
  border-color: #73b8ff; }

.xl-radio:hover:not(.xl-radio--disabled).xl-radio--checked .xl-radio__inner:after {
  background-color: #73b8ff; }

.xl-radio-group {
  position: relative; }
  .xl-radio-group .xl-radio:not(:last-child) {
    margin-right: 0.5em; }

.xl-radio-group--button {
  *zoom: 1; }
  .xl-radio-group--button:after {
    content: "";
    display: table;
    clear: both; }
  .xl-radio-group--button .xl-radio {
    float: left;
    border: 1px solid #dddee1;
    padding: 0.4em 0.6em;
